import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { useSelector } from "react-redux";
import { Grid } from "@material-ui/core";
import Fade from "react-reveal/Fade";
import { motion } from "framer-motion";
import { whileStatement } from "@babel/types";

const useStyles = makeStyles((theme) => ({
  root: {
    overflow: "auto",
    paddingTop: "75px",
    paddingBottom: "75px",
  },
  sectionTitleText: {
    marginTop: "0px",
    fontFamily: "IntegralCF, sans-serif",
    fontSize: "1.5em",
    lineHeight: "1em",
    fontWeight: "900",
    letterSpacing: "1px",
    textAlign: "center",
  },
  sectionTitleDivider: {
    width: "50px",
    height: "3px",
    marginLeft: "auto",
    marginRight: "auto",
    marginBottom: "75px",
  },
  form: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
  },
  infoField: {
    width: "80vw",
    maxWidth: "450px",
    fontFamily: "Sofia Pro, sans-serif",
    fontSize: "16px",
    lineHeight: "27px",
    fontWeight: "300",
    border: "none",
    margin: "5px",
    padding: "10px",
  },
  messageField: {
    width: "80vw",
    maxWidth: "450px",
    fontFamily: "Sofia Pro, sans-serif",
    fontSize: "16px",
    lineHeight: "27px",
    fontWeight: "300",
    resize: "none",
    border: "none",
    margin: "5px",
    padding: "10px",
  },
  submitButton: {
    width: "125px",
    height: "50px",
    fontFamily: "Sofia Pro, sans-serif",
    fontSize: "16px",
    lineHeight: "27px",
    fontWeight: "300",
    backgroundColor: "white",
    border: "solid 3px",
    margin: "5px",
    cursor: "pointer",
  },
  submitButtonText: {
    margin: "0px",
  },
}));

const Contact = () => {
  const classes = useStyles();
  const color = useSelector((state) => state.colorPallete);

  return (
    <div
      id="contact"
      className={classes.root}
      style={{ color: color.mutedDark }}
    >
      <Fade left cascade>
        <h1 className={classes.sectionTitleText}>Contact</h1>
        <div
          className={classes.sectionTitleDivider}
          style={{ backgroundColor: color.mutedDark }}
        />
      </Fade>
      <form className={classes.form} action="submit" autoComplete="on">
        <Fade up cascade>
          <input
            className={classes.infoField}
            style={{
              color: color.mutedDark,
              backgroundColor: color.mutedLight,
            }}
            type="text"
            name="name"
            placeholder="Name"
          />
          <input
            className={classes.infoField}
            style={{
              color: color.mutedDark,
              backgroundColor: color.mutedLight,
            }}
            type="text"
            name="email"
            placeholder="Email"
          />
          <textarea
            className={classes.messageField}
            style={{
              color: color.mutedDark,
              backgroundColor: color.mutedLight,
            }}
            name="message"
            placeholder="Your Message"
            cols="30"
            rows="7"
          ></textarea>
        </Fade>
        <motion.button
          className={classes.submitButton}
          style={{ borderColor: color.accent, color: color.accent }}
          whileHover={{
            color: color.mutedLight,
            backgroundColor: color.accent,
          }}
        >
          <h3 className={classes.submitButtonText}>Submit</h3>
        </motion.button>
      </form>
    </div>
  );
};

export default Contact;
